'use client';

import React, { useState } from 'react';
import { useTranslations, useLocale } from 'next-intl';
import { motion } from 'framer-motion';
import { Mail, Phone, MapPin, Instagram, ArrowRight, MessageCircle, ChevronDown, Compass, Globe } from 'lucide-react';
import { siteInfo } from '@/data/siteInfo';
import Image from 'next/image';

export default function ContactPage() {
    const t = useTranslations('ContactPage');
    const locale = useLocale();
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: ''
    });

    const handleWhatsAppSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        const text = `*New Inquiry from Website*\n\n*Name:* ${formData.name}\n*Email:* ${formData.email}\n*Message:* ${formData.message}`;
        const encodedText = encodeURIComponent(text);
        const whatsappUrl = `https://wa.me/${siteInfo.contact.whatsapp.replace(/\+/g, '')}?text=${encodedText}`;
        window.open(whatsappUrl, '_blank');
    };

    return (
        <div className="min-h-screen bg-surface-off">
            {/* Header Section */}
            <section className="relative h-[60vh] w-full flex items-center justify-center overflow-hidden">
                <div className="absolute inset-0">
                    <Image
                        src="/images/toubkal_treks/toubkal_trekking_mountains.jpg"
                        alt="Contact Morocco Atlas Guide"
                        fill
                        className="object-cover"
                        priority
                    />
                    <div className="absolute inset-0 bg-gradient-to-b from-black/60 via-black/10 to-surface-off" />
                </div>

                <div className="relative z-10 text-center px-6">
                    <motion.div
                        initial={{ opacity: 0, y: 30 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.8 }}
                    >
                        <span className="inline-block py-1.5 px-4 rounded-lg bg-primary/20 backdrop-blur-md text-primary-light text-sm font-bold uppercase tracking-widest mb-6 border border-primary/20">
                            Get in Touch
                        </span>
                        <h1 className="text-6xl md:text-8xl font-bold text-white font-outfit mb-6 tracking-tight">
                            Let's <span className="text-primary italic">Connect</span>
                        </h1>
                        <p className="text-white/80 text-xl md:text-2xl max-w-2xl mx-auto font-light leading-relaxed">
                            Planning your next mountain escape? We're here to help you every step of the way.
                        </p>
                    </motion.div>
                </div>
            </section>

            {/* Main Content */}
            <section className="py-24">
                <div className="container mx-auto px-6">
                    <div className="grid grid-cols-1 lg:grid-cols-12 gap-16 items-start">

                        {/* Left Side: Contact Cards */}
                        <div className="lg:col-span-5 space-y-6">
                            <motion.div
                                initial={{ opacity: 0, x: -30 }}
                                whileInView={{ opacity: 1, x: 0 }}
                                viewport={{ once: true }}
                            >
                                <h2 className="text-4xl font-bold text-neutral-dark font-outfit mb-8 tracking-tight">Contact <span className="text-primary italic">Channels</span></h2>

                                <div className="space-y-4">
                                    {/* Phone/WhatsApp Card */}
                                    <a href={`https://wa.me/${siteInfo.contact.whatsapp.replace(/\+/g, '')}`} className="group block bg-white border border-neutral-100 p-8 rounded-xl shadow-sm hover:shadow-2xl hover:border-primary/20 transition-all duration-500">
                                        <div className="flex items-center gap-6">
                                            <div className="w-16 h-16 rounded-xl bg-green-50 flex items-center justify-center group-hover:bg-green-500 transition-colors duration-500">
                                                <MessageCircle className="w-8 h-8 text-green-500 group-hover:text-white transition-colors duration-500" />
                                            </div>
                                            <div>
                                                <span className="block text-xs font-bold text-neutral-400 uppercase tracking-widest mb-1">WhatsApp / Call</span>
                                                <span className="text-2xl font-bold text-neutral-dark font-outfit group-hover:text-primary transition-colors">{siteInfo.contact.phone}</span>
                                            </div>
                                        </div>
                                    </a>

                                    {/* Email Card */}
                                    <a href={`mailto:${siteInfo.contact.email}`} className="group block bg-white border border-neutral-100 p-8 rounded-xl shadow-sm hover:shadow-2xl hover:border-primary/20 transition-all duration-500">
                                        <div className="flex items-center gap-6">
                                            <div className="w-16 h-16 rounded-xl bg-primary/5 flex items-center justify-center group-hover:bg-primary transition-colors duration-500">
                                                <Mail className="w-8 h-8 text-primary group-hover:text-white transition-colors duration-500" />
                                            </div>
                                            <div>
                                                <span className="block text-xs font-bold text-neutral-400 uppercase tracking-widest mb-1">Email Inquiry</span>
                                                <span className="text-xl font-bold text-neutral-dark font-outfit group-hover:text-primary transition-colors">{siteInfo.contact.email}</span>
                                            </div>
                                        </div>
                                    </a>

                                    {/* Location Info */}
                                    <div className="bg-white border border-neutral-100 p-8 rounded-xl shadow-sm flex items-center gap-6">
                                        <div className="w-16 h-16 rounded-xl bg-stone-100 flex items-center justify-center">
                                            <MapPin className="w-8 h-8 text-stone-600" />
                                        </div>
                                        <div>
                                            <span className="block text-xs font-bold text-neutral-400 uppercase tracking-widest mb-1">Our Base</span>
                                            <span className="text-xl font-bold text-neutral-dark font-outfit">{siteInfo.contact.address}</span>
                                        </div>
                                    </div>

                                    {/* Social */}
                                    <div className="pt-8 border-t border-neutral-100 mt-8 flex items-center gap-6">
                                        <span className="font-bold text-sm text-neutral-400 uppercase tracking-widest">Follow the Trail</span>
                                        <div className="flex gap-4">
                                            <a href={siteInfo.socials.instagram} className="w-12 h-12 rounded-full border border-neutral-100 flex items-center justify-center hover:bg-neutral-dark hover:text-white transition-all shadow-sm">
                                                <Instagram className="w-5 h-5" />
                                            </a>
                                            <a href={siteInfo.socials.youtube} className="w-12 h-12 rounded-full border border-neutral-100 flex items-center justify-center hover:bg-neutral-dark hover:text-white transition-all shadow-sm">
                                                <Globe className="w-5 h-5" />
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </motion.div>
                        </div>

                        {/* Right Side: Inquiry Form */}
                        <div className="lg:col-span-7">
                            <motion.div
                                initial={{ opacity: 0, y: 30 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                viewport={{ once: true }}
                                transition={{ delay: 0.2 }}
                                className="bg-white rounded-[3rem] p-10 md:p-16 shadow-2xl shadow-primary/5 border border-neutral-100 relative overflow-hidden"
                            >
                                {/* Decorative elements */}
                                <div className="absolute top-0 right-0 w-32 h-32 bg-primary/5 rounded-full blur-3xl" />
                                <div className="absolute bottom-0 left-0 w-48 h-48 bg-primary/5 rounded-full blur-3xl" />

                                <div className="relative z-10">
                                    <h2 className="text-3xl md:text-4xl font-bold text-neutral-dark font-outfit mb-4">Send a <span className="text-primary italic">Message</span></h2>
                                    <p className="text-neutral-medium font-light mb-12 max-w-md">Tell us about your dream trip and we'll help you make it a reality. We usually reply within 2 hours.</p>

                                    <form onSubmit={handleWhatsAppSubmit} className="space-y-8">
                                        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                                            <div className="space-y-2">
                                                <label className="text-xs font-bold text-neutral-dark uppercase tracking-widest ml-1">Full Name</label>
                                                <input
                                                    type="text"
                                                    required
                                                    value={formData.name}
                                                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                                                    placeholder="e.g. Sarah J."
                                                    className="w-full bg-neutral-50 border-none rounded-lg px-8 py-5 text-neutral-dark outline-none ring-1 ring-neutral-100 focus:ring-2 focus:ring-primary/40 transition-all font-medium"
                                                />
                                            </div>
                                            <div className="space-y-2">
                                                <label className="text-xs font-bold text-neutral-dark uppercase tracking-widest ml-1">Email Address</label>
                                                <input
                                                    type="email"
                                                    required
                                                    value={formData.email}
                                                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                                                    placeholder="sarah@example.com"
                                                    className="w-full bg-neutral-50 border-none rounded-lg px-8 py-5 text-neutral-dark outline-none ring-1 ring-neutral-100 focus:ring-2 focus:ring-primary/40 transition-all font-medium"
                                                />
                                            </div>
                                        </div>

                                        <div className="space-y-2">
                                            <label className="text-xs font-bold text-neutral-dark uppercase tracking-widest ml-1">Your Message / Plans</label>
                                            <textarea
                                                rows={6}
                                                required
                                                value={formData.message}
                                                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                                                placeholder="I would like to trek Mount Toubkal in December..."
                                                className="w-full bg-neutral-50 border-none rounded-lg px-8 py-5 text-neutral-dark outline-none ring-1 ring-neutral-100 focus:ring-2 focus:ring-primary/40 transition-all font-medium resize-none"
                                            ></textarea>
                                        </div>

                                        <button
                                            type="submit"
                                            className="w-full bg-neutral-dark text-white p-6 rounded-[2rem] font-bold text-lg hover:bg-neutral-800 transition-all transform active:scale-95 flex items-center justify-center gap-4 group"
                                        >
                                            <MessageCircle className="w-6 h-6 text-green-400 group-hover:scale-110 transition-transform" />
                                            Submit via WhatsApp
                                            <ArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
                                        </button>
                                    </form>
                                </div>
                            </motion.div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
